<template lang="pug">
	div
		header.header
			.header-button#button_restart(v-touch:tap="restart") RESTART
			.header-button#button_hint(v-touch:tap="hint") HINT
		#words
			.word(v-for="word in words_status" :class="{active: word.active, hint: word.hint}")
				.word-letter(v-for="letter in word.w")
					span.word-letter-inner {{ letter }}

		#letters
			.letter(v-for="(letter, li) in letters" :class="{active: input_letters.indexOf(li) >= 0, 'active-last': input_letters.indexOf(li) + 1 == input_letters.length}" v-touch:tap="tapLetter(li)") {{ letter }}

		#input_letters
			span(v-for="li in input_letters") {{ letters[li] }}

		#modal(:class="{active: is_generating || is_won}")
			.modal-content(v-if="is_generating") 
				span Creating Game<br>For You. <br>Be patient!
			.modal-content(v-if="is_won")
				span You Won!
				.modal-content-button(v-touch:tap="restart") RESTART



</template>


<script>
import cis from './cis.json'

export default {
	data() {
		return {
			title: 'Words Game',
			all_words: cis,
			number_letters: 5,
			letters: [],
			letters_status: [],
			alphabet: 'abcdefghijklmnopqrstuvwxyz',
			is_found: false,
			generated_words: [],
			words: [],
			words_status: [],
			has_long_word: false,
			input_letters: [],
			correct_words: [],
			is_generating: false,
			is_won: false,

		}
	},

	methods: {
		restart() {
			this.is_won = false
			this.is_generating = true
			
			setTimeout(() => {
				this.generate()
			}, 1000)

		},

		generate() {
			this.is_found = false;
			this.generated_words = [];

			while (!this.is_found) { 
				// generate letters
				this.letters = []
				// this.letters_status = []
				this.generated_words = [];
				this.has_long_word = false
				while (this.letters.length < this.number_letters) {
					var letter = this.alphabet[Math.floor(Math.random() * this.alphabet.length)]
					// console.log(letter)
					// 1 letter could appear twice
					if ((this.letters.join().match(new RegExp(letter, "g")) || []).length < 2) {
						this.letters.push(letter)
						// this.letters_status.push({l: letter, active: false})
					}
				}

				findWords:
				for (let word of this.all_words) {
					if (word.length >= 3 && word.length <= this.number_letters) {
						var letter_indexes = []
						for (let letter of word) {
							var letter_ok = false
							for (let li in this.letters) {
								if (letter_indexes.indexOf(li) < 0 && letter == this.letters[li]) {
									letter_ok = true
									letter_indexes.push(li)
									break
								}
							}

							if (!letter_ok) continue findWords
						}

						this.generated_words.push(word);

					}
				}

				if (this.generated_words.length >= this.number_letters + 3) {
					var all_ok = false
					for (var word of this.generated_words) {
						if (word.length == this.number_letters) {
							all_ok = true
						}
					}

					if (all_ok) {
						this.is_found = true;
						this.is_generating = false
					}

				}

			}
			// console.log('Found')

			// after found words
			var found_ok = false
			while (!found_ok) {
				this.words = []
				this.words_idx = []
				this.words_status = []


				while (this.words.length < this.number_letters + 3) {
					var idx = Math.floor(Math.random() * this.generated_words.length)
					if (this.words.indexOf(this.generated_words[idx]) < 0) {
						this.words.push(this.generated_words[idx])
						this.words_status.push({w: this.generated_words[idx], active: false, hint: false})
					}
				}

				// check if has longest
				for (let word of this.words) {
					if (word.length == this.number_letters) {
						found_ok = true
						break
					}
				}
			}
			// found

		},

		tapLetter(li) {
			return function() {
				// console.log("tap: ", li)
				if (this.input_letters.indexOf(li) < 0) {
					this.input_letters.push(li)
	
					// check the input word
					var input_word = ''
					for (let li of this.input_letters) input_word += this.letters[li]
					// console.log(input_word)

					var found_one = false
					findCorrect:
					for (let word of this.generated_words) {
						if (input_word == word) {
							// console.log('found word')
							for (let wi in this.words_status) {
								if (input_word.length == this.words_status[wi].w.length 
								&& !this.words_status[wi].active
								&& this.correct_words.indexOf(input_word) < 0 ) {
									this.words_status[wi].w = input_word
									this.words_status[wi].active = true
									this.words_status[wi].hint = false
									this.correct_words.push(input_word)
									this.input_letters = []
									found_one = true
										// clear
									for (var i in this.words_status) {
										this.words_status[i].hint = false
									}
									break findCorrect
								}
							}
						}
					}

					if (found_one) {
						// check if all words
						var is_all = true
						for (let word of this.words_status) {
							if (!word.active) {
								is_all = false
							}
						}
						
						if (is_all) {
							// Won
							// console.log('you won!')
							this.is_won = true

						}

					}
	
				} else if (this.input_letters.indexOf(li) >= 0 && this.input_letters[this.input_letters.length - 1] == li) {
					this.input_letters.pop()
				}
			}.bind(this)
		},

		hint() {
			// get inactive
			var inactive_length = []
			for (let word of this.words_status) {
				if (!word.active && !word.hint && inactive_length.indexOf(word.length) < 0) {
					inactive_length.push(word.w.length)
				}
			}
			// console.log(inactive_length)
			// 
			var random_length = inactive_length[Math.floor(Math.random() * inactive_length.length)]
			// get words with same length but not in correct_words
			var possible_words = []
			for (let word of this.generated_words) {
				if (word.length == random_length && possible_words.indexOf(word) < 0 && this.correct_words.indexOf(word) < 0) {
					possible_words.push(word)
				}
			}
			// console.log(possible_words)
			// put in 
				// clear hint
			for (let i in this.words_status) {
				this.words_status[i].hint = false
			}

			for (let i in this.words_status) {
				if (!this.words_status[i].active && this.words_status[i].w.length == random_length) {
					this.words_status[i].w = possible_words[Math.floor(Math.random() * possible_words.length)]
					this.words_status[i].hint = true
					break
				}
			}

		}

	}
}
</script>

<style lang="scss" scoped>
header.header {
	height: 65px;
	color: #fff;
	text-align: center;

	.header-button {
		width: 50%;
		display: inline-block;
		text-align: center;
		line-height: 65px;
		height: 100%;
		margin: 0;
		border: 0;
		outline: none;
		padding: 0;
		color: #fff;
		font-family: Helvetica;
		font-weight: bold;
		font-size: 30px;
		user-select: none;

		&.active {
			opacity: .2;
		}

		&#button_restart {
			background-color: #a3b7b9;
		}

		&#button_hint {
			background-color: #dab3a7;
		}
	}

}
// words
#words {
	.word {
		width: auto;
		height: auto;
		display: inline-block;
		margin-right: 10px;
		margin-left: 10px;
		margin-top: 10px;
		padding: 0;

		&.active {
			.word-letter {
				background-color: #e0ce9f;

				span.word-letter-inner {
					visibility: visible;
				}
			}

		}

		&.hint {
			.word-letter:nth-child(1) {
				background-color: #e0ce9f;

				span.word-letter-inner {
					visibility: visible;
				}
			}
		}

		.word-letter {
			width: 30px;
			height: 30px;
			line-height: 28px;
			margin: 0;
			border-top: 3px solid #a3b7b9;
			border-right: 3px solid #a3b7b9;
			border-bottom: 3px solid #a3b7b9;
			text-align: center;
			display: inline-block;
			background-color: #f6ebd8;
			color: #666;
			font-family: Helvetica;
			font-weight: bold;
			font-size: 22px;

			&:nth-child(1) {
				border-left: 3px solid #a3b7b9;
			}

			span.word-letter-inner {
				visibility: hidden;
			}
		}
		
	}

}

#letters {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 40%;
	background-color: #666;

	.letter {
		width: 60px;
		height: 60px;
		line-height: 60px;
		border-radius: 8px;
		background-color: rgb(230, 230, 36);
		text-align: center;
		font-family: Helvetica;
		font-size: 40px;
		color: #666;
		font-weight: bold;
		position: absolute;

		&.active {
			background-color: #a13b0b;
			color: #ccc;

			&.active-last {
				background-color: #ee763f;
			}
		}

		&:nth-child(1) {
			top: 8%;
			left: 22%;
			transform: rotate(15deg);
		}

		&:nth-child(2) {
			top: 35%;
			left: 10%;
			transform: rotate(-15deg);
		}

		&:nth-child(3) {
			top: 65%;
			left: 26%;
			transform: rotate(12deg);
		}

		&:nth-child(4) {
			top: 5%;
			right: 30%;
			transform: rotate(-15deg);
		}

		&:nth-child(5) {
			top: 40%;
			right: 20%;
			transform: rotate(15deg);
		}

		&:nth-child(6) {
			top: 70%;
			right: 30%;
			transform: rotate(15deg);
		}

	}

}

#input_letters {
	position: absolute;
	bottom: 40%;
	left: 0;
	width: 100%;
	height: 30px;
	text-align: center;
	font-family: Helvetica;
	font-weight: bold;
	font-size: 21px;
	color: #000;
}

#modal {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 9999;
	background-color: rgba(255, 255, 255, 0.8);
	display: none;

	&.active {
		display: flex;
	}

	.modal-content {
		width: 100%;
		height: auto;
		margin-top: auto;
		margin-bottom: auto;
		text-align: center;
		font-family: Helvetica;
		font-size: 30px;
		font-weight: bold;
		color: #a3b7b9;

		.modal-content-button {
			width: 200px;
			height: 50px;
			line-height: 50px;
			text-align: cener;
			margin-left: auto;
			margin-right: auto;
			margin-top: 30px;
			background-color: #f6d1b4;
			color: #fff;
			font-size: 22px;

		}
	}
}
</style>